import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue'
export default {
	components: {
		uniLoadMore
	},
	data() {
		return {
			lists: [],
			page: 1, //当前页
			limit: 10, //每页显示几条
			loadStatus: 'more'
		};
	},
	onLoad () {
		this.getShareCode();
		this.getDataList();
	},
	onReachBottom () {
		if (this.loadStatus === 'more') {
			this.getDataList()
		}
	},
	methods: {
		getDataList() {
			this.loadStatus = 'loading'
			let data = {
				page: this.page,
				limit: this.limit
			}
			this.$api.recommendList(data, res => {
				if (res.status) {
				    for (let i = 0; i < res.data.length; i++) {
						if (res.data[i].avatar == null) {
							res.data[i].avatar = this.$store.state.config.shop_default_image;
						}
						if (res.data[i].nickname == null) {
							res.data[i].nickname = '暂无昵称'
						}
				    }
					let lists = this.lists.concat(res.data);
					this.lists = lists;
					if (res.total > this.page) {
						this.page++
						this.loadStatus = 'more'
					} else {
						this.loadStatus = 'noMore'
					}
				}else{
					this.$common.errorToShow(res.msg)
				}
			});
		},
		//获取邀请码
		getShareCode(){
			let userToken = this.$db.get("userToken");
			if (userToken && userToken != '') {
				// 获取我的分享码
				this.$api.shareCode({}, res => {
					if (res.status) {
						this.myShareCode = res.data;
					}
				});
			}
		}
	},
};
